:root {
  --bg-color-main: #ececec;
  --color-main: #3e3e3e;
  --color-white: #fff --color-black: #000;
}

.page-header {
  width: 100vw;
  height: 11.875rem;
  background-image: url("Rectangle 53.ac23ad4b.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.page-header__navbar {
  width: 100%;
  height: 3.75rem;
  background-color: var(--bg-color-main);
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header__logo {
  width: 3.125rem;
  height: 1.875rem;
  fill: var(--color-main);
  stroke: var(--color-main);
}

.profile-container {
  width: 18.75rem;
  height: calc(100vh - 15.625rem);
  background-color: #000;
}

* {
  box-sizing: border-box;
}

